<template>
	<div>
		<el-col :span="24" class="warp-breadcrum">
      <div id="myChart" :style="{width: '1200px', height: '600px', margin: 'auto' }"></div>
    </el-col>
    <el-col :span="24" style="margin-bottom:10px;margin-top:40px;">
      <div class="grid-content bg-purple-dark fl">
        <span><i class="i_col_red"><span>{{tableData.saleAllMoney}}</span></i></span>
        <span class="margin_l"><i class="i_col_red">{{tableData.haocaiAllMoney}}</i></span>
        <span class="margin_l"><i class="i_col_red">{{tableData.gongjuAllMoney}}</i></span>
      </div>
      <div class="grid-content bg-purple-dark fr">
        <span class="margin_l"><i class="i_col_red">{{tableData.orderNum}}</i></span>
      </div>
    </el-col>
		<el-col :span="24" class="warp-main" style="margin: auto;margin-bottom:100px;">
      <el-table :data="tableData.myOrderVoList" align="center" border style="width: 100%">
        <el-table-column type="index" align="center" label="">
        </el-table-column>
        <el-table-column prop="orderTime" align="center" label="">
        </el-table-column>
        <el-table-column prop="custName" align="center" label="">
        </el-table-column>
        <el-table-column prop="custPhone" align="center" label="">
        </el-table-column>
        <el-table-column prop="saleAmt" align="center" label="">
        </el-table-column>
        <el-table-column prop="materialAmt"  align="center"label="">
        </el-table-column>
        <el-table-column prop="equipAmt" align="center" label="">
        </el-table-column>
        <el-table-column prop="refundAmt" align="center" label="">
        </el-table-column>
        <el-table-column prop="realSaleAmt" align="center" label="">
        </el-table-column>
        <el-table-column prop="handler" align="center" label="">
          <template scope="scope">
            <el-button
              size="mini"
              type="info"
              @click="queryDetail(scope.$index, scope.row)"></el-button>
          </template>  
        </el-table-column>
      </el-table>
    </el-col>

    <!--  -->
    <el-dialog
      :visible.sync="detailVisible"
      size="small">
      <div class="detail_title"></div>
      <div class="custInfo"></div>
      <div class="custContent"><b></b><span></span><b style="margin-left:80px;"></b><span>13861637946</span></div>
      <div class="custInfo"></div>
      <table class="datail_tb">
        <tr><td colspan="7"><span class="pad_l_30">2017-06-08</span><span class="pad_l_30"></span></td></tr>
        <tr class="trs tr_title">
          <th>+</th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr class="trs" v-for="(item, index) in infoList" :key="index">
          <td>{{item.goodsName}}</td>
          <td>{{item.price}}</td>
          <td>{{item.num}}</td>
          <td>{{item.totalPrice}}</td>
        </tr>
        <tr class="trs tr_title">
          <td colspan="3"></td>
          <td>180</td>
        </tr>
      </table>
      <div class="custInfo" style="margin-top:20px;"></div>
      <el-table class="datail_tb" :data="orderDetailList" border show-summary     style="width: 100%">
        <el-table-column prop="itemName" label="" align="center">
        </el-table-column>
        <el-table-column prop="salesAmt" label="" align="center">
        </el-table-column>
        <el-table-column prop="refundAmt" label="" align="center">
        </el-table-column>
        <el-table-column prop="realSaleAmt" label="" align="center">
        </el-table-column>
      </el-table>
      <div style="text-align:center;margin-top:20px;">
        <el-button @click="detailVisible = false"> </el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script>
import global from '../../global/global'
let echarts = require('../../../../node_modules/echarts/lib/echarts')
// 
require('echarts/lib/chart/line');
// title
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');

export default {
  props: ['orderInfo','echartData','echartsTitle','monthX'],
  data() {
    return {
      detailVisible: false,
      tableData: null,
      monthxVal: 30,
      monthList: [],
      infoList: [{
        goodsName: '1',
        price: 30,
        num: 2,
        totalPrice: 60
      },{
        goodsName: '2',
        price: 30,
        num: 2,
        totalPrice: 60
      },{
        goodsName: '3',
        price: 30,
        num: 2,
        totalPrice: 60
      }],
      orderDetailList: [{
        itemName: '',
        salesAmt: 3000,
        refundAmt: 200,
        realSaleAmt: 2800
      },
      {
        itemName: '',
        salesAmt: 4000,
        refundAmt: 100,
        realSaleAmt: 3900
      }]
    }
  },
  created(){
    this.tableData = this.orderInfo
    for(var i=0;i<this.monthxVal+1;i++){
      this.monthList.push(i)
    }
  },
  watch: {
    echartsTitle: function(){
      this.drawLine();
    },
    monthX: function(){
      this.drawLine();
    }
  },
  mounted() {
    this.drawLine();
  },
	methods: {
		drawLine(){
			// domecharts
      let myChart=document.getElementById('myChart')

      let mainChart = echarts.init(myChart)// domecharts
      // 
      mainChart.setOption({
          title: {
              text: this.echartsTitle,
              left: 'center'
          },
          tooltip: {
              trigger: 'axis'
          },
          legend: {
              data:['']
          },
          grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
          },
          toolbox: {
              feature: {
                  saveAsImage: {}
              }
          },
          xAxis: {
              type: 'category',
              name: this.monthX+"",
              boundaryGap: false,
              axisLabel:{
                 //X
                interval: 4 //0auto:
              },
              data: this.monthList
          },
          yAxis: {
              type: 'value',
              name: '',
              max: 4000,
              min: 0,
              splitNumber: 10,
          },  
          series: [
              {
                  name:'',
                  type:'line',
                  stack: '',
                  data: [1, 3, 9, 27, 81, 247, 741,1, 3, 9, 27, 81, 247, 741,1, 3, 9, 27, 81, 247, 741,1, 3, 9, 27, 81, 247, 741,1,3800,2000]
              }
          ]
      });

		},
    queryDetail(index, row){
      let params = {
        userPhone: row.userPhone,
        orderId: row.saleId,
        token: global.getSalesToken()
      }
      console.log('',params)
      global.axiosGetReq('/saleMyOrder/detail',params).then((res) => {
        if (res.data.callStatus === 'SUCCEED') { 
          console.log('',res.data.data)
          
        }else{
          this.$message.error('');
        }
      })
      this.detailVisible = true
    }
	}
}
</script>

<style scope>
	.brandWarp{
    width: 1200px;
    margin: auto;
  }
  .fl{
    float:left;
  }
  .fr{
    float: right;
  }
  .clearfix{
    zoom:1;
  }
  .clearfix:after{
    content:"";
    clear:both;
    display:block;
  }
  .i_col_red{
    color: red;
    font-weight: bold;
    font-style: normal;
  }
  .margin_l{
    margin-left: 20px;
  }
  .total_box{
    width: 300px;
    height: 44px;
    font-size: 14px;
    line-height: 44px;
    background: #5db7e8;
    color: #fff;
    text-align: center;
  }
  .warp-breadcrum{
    margin-top: 36px;
  }
  .pad_l_30{
    padding-left: 30px;
  }
  .custInfo{
    height: 30px;
    line-height: 30px;
    padding-left: 24px;
    border-bottom: 1px dotted #EEF1F6;
    background: url(../../../images/salesman/arrow.png) 0 8px no-repeat;
  }
  .detail_title{
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    background: url(../../../images/salesman/titlebg.png) 400px 34px no-repeat;
  }
  .datail_tb{
    width: 100%;
    border-collapse: collapse;/*   */
    margin-top: 20px;
  }
  .custContent{
    height: 70px;
    line-height: 70px;
    padding-left: 52px;
  }
  .datail_tb tr{
    width: 100%;
    height: 40px;
    line-height: 40px;
    border: 1px solid #EEF1F6;
  }
  .trs td{
    width: 100px;
    text-align: center;
    border: 1px solid #EEF1F6;
  }
  .tr_title{
    background: #EEF1F6;
  }
  .tr_title td, .tr_title th{
    border: 1px solid #E8EAED;
  }
</style>


